// import { ADD_TODO, TOGGLE_TODO, SET_FILTER } from "./action-types";
export const increment = () => ({ 
  type: "INC",
});
export const decrement = (num) => ({
  type: "DEC",
  payload: {
    num
  }
});
export const reset = () => ({ type: "RESET" });